
.mainpage_header_title {
  width: 100%;
  height: 8vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed; 
  top: 0; 
  left: 0; 
  background-color: white; 
  z-index: 1000; 
  opacity: 0.8;
}

.mainpage_header_category {
  z-index: 1000; 
  width: 100%;
  position: fixed; 
  top: 8vh; 
  left: 0; 
}

.mainpage_header_category nav {
  height: 5vh;
  width: 100%;
  background-color: aliceblue;
  opacity: 0.8;
}

.mainpage_header_category ul {
  justify-content: space-evenly;
  align-items: center;
  border: 1px solid black;
  height: 100%;
  display: flex;
  list-style: none;
  padding: 0;
  width: 100%; 
  
}

.mainpage_header_category li {
  min-width: 50px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  margin-right: 10px;
  transition: all 0.3s  ease-in-out;
  position: relative; /* 부모 요소로부터의 상대적인 위치를 설정합니다. */
  
}
.mainpage_header_category li:hover {
  cursor: pointer;
  transform: scale(1.1); /* 'scale' 속성을 'transform' 속성으로 수정합니다. */
}

.mainpage_header_category li:hover::before {
  content: '•'; 
  position: absolute; 
  top: -15px; 
  left: 50%;
  transform: translateX(-50%); 
  font-size: 20px; 
}

.mainpage_body_gap{
   margin-bottom: 5vw;
}

.slide_container{
    display: flex;
    justify-content: center;
    margin: 0 auto;
}
.slide_show{
    width: 80%;
    height: 70vh;
    text-align: center;
    font-size: 40px;
    overflow: hidden;
}

.slide_images img{
  border-radius: 5%;
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
}
.slide_images{
  width: 100%;
  height: 100%;
  flex-shrink: 0; /*이 구역안에 생기는 영역들이 공간을 분배하여 가지지 않도록 적용*/
  font-size: 40px;
  text-align: center;
    position: relative;
 
}
.slide_buttons{
    border: 1px solid black;
   height: inherit;
}
.slide_images button {
  margin: 5px;
}
.profile_container{
  width:  80%;
  height: 40vh;
   display: grid;
   grid-template-columns: auto 1fr; /* 이미지와 텍스트 영역을 1:1 비율로 배치 */
   grid-gap: 20px; /* 이미지와 텍스트 사이의 간격 설정 */

   margin : 0 auto;
  background-size: cover;
  background-image: url(/public/img/background/paper.jpg);
}
.profile_img{
  position: relative;
  width: 100%;
  height:inherit;
}
.profile_img img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.profile_img_title {
  width: 100%;
  text-align: center;
  position: absolute;
  bottom: 10px; /* 원하는 위치로 조정 */
  color: white;
  /* 다른 스타일 */
}
.profile_info{
  height: 100%;
  width: 100%;
 
}
.profile_info_content{
  font-size: 20px;
  font-weight: bold;
  height: inherit;


}
.profile_info_content ul {
  height: 100%;
  display: flex; /* Flexbox를 사용합니다. */
  justify-content: center;
  flex-direction: column;
  gap: 10px; 
}

.profile_info_content li {
  flex: 1; 
  padding: 10px; 
}

