.woodhouse_container {
    width: 100%;
    min-height: 100vh;
    height: 100%;
    display: flex;
}

.woodhouse_sidebar{

    margin-left: 1%;
    position: fixed;
    width: 15%;
   overflow: hidden;
  
    height: 60vh;
    border: 1px solid black;
    border-radius: 30px;
    background-size: cover;
  background-image: url(/public/img/background/sidepaper2.jpg);
  box-shadow: 1px 2px 2px rgba(0, 0, 0, 0.5);
}
.woodhouse_sidebar_title{
     display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; /* 수직으로 중앙 정렬합니다. */
    border-bottom: 1px solid black;
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    background-color: white;
    text-shadow: 0px 2px 2px rgba(0, 0, 0, 0.5);
    height: 10%;
}

.woodhouse_sidebar ul {
  margin-left: 10px;

  padding: 0;
  flex-direction: column; 
  justify-content: space-between;
  height: 100%; 
}

.woodhouse_sidebar li {
  padding: 10px 0; 
  height: calc(100% / 4); 
  margin-left: 20px;
  text-decoration: underline;
  font-size: 22px;
  font-weight: bold;
  cursor: pointer;
}
.woodhouse_sidebar li:first-child {
  margin-top: 20px;
}

.woodhouse_content{
    margin-left: 20%;
    flex : 1;
    height: 100%;
    min-height: 100vh;
  
}
.woodhouse_content_result,
.woodhouse_content_process,
.woodhouse_content_info{

  border: 3px solid black;
  min-height: 60vh;
  height: 100%;
  width: 77vw;
  margin-bottom: 15px;
  border-radius: 30px;
   position: relative;
}

.woodhouse_content_reulst_title,
.woodhouse_content_process_title,
.woodhouse_content_info_title{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; /* 수직으로 중앙 정렬합니다. */
    border-bottom: 3px solid black;
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    height: 80px;
}

.woodhouse_content_info_body {
  display: flex;
  align-items: center;
  height: 55vh;
 
}
.woodhouse_content_info_body_image{
  margin-left: 15px;
  margin-top:  15px;

  width: 30%;
  height: 90%;
  
}
.woodhouse_content_info_body_image img{
  object-fit: fill;
  width: 95%;
  height: 100%;
  border-radius: 10%;
}
 .woodhouse_content_info_body_text {
  border-left: 3px solid black;
  margin-left: 1vw;
  padding-left: 30px;
  height: 100%;
  
 }
.woodhouse_content_info_body_text ul {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 100%; /* 부모 컨테이너의 높이에 맞게 설정 */
  font-size: 20px;

}


.woodhouse_content_process_body_img {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly; /* 각 줄의 양 끝에 이미지를 배치합니다. */
}

.woodhouse_content_process_body_img_item {
  margin-top: 40px;
  width: calc(26% - 20px); /* 이미지의 너비를 조정하고 여유 공간을 고려합니다. */
 margin-left: 10px;
 margin-bottom: 60px;
 cursor: pointer;
  transition: transform 0.3s ease;

}
.woodhouse_content_process_body_img_item img{
 width: 100%; /* 이미지를 부모 요소의 너비에 맞게 조정합니다. */
 height: 100%;
   border-radius: 30px;
  
 
}
.woodhouse_content_process_body_img_item:hover{
  transform: scale(1.1);
}
.woodhouse_content_process_body_img_item h2{
  text-align: center; 

}
.modal {
    position: absolute; 
    top: 0; 
    left: 0;
    width: 100%; 
    height: 100%; 
    background-color: white;
    border-radius: 30px;
 
}
.modal_body {
  height: 100%;
}
.modal_body img{
  height: 40%;
  width: 30%;
  cursor: pointer;
  border-radius: 30px;
  
}
.modal_title{
   display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; /* 수직으로 중앙 정렬합니다. */
    border-bottom: 3px solid black;
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    height: 80px;
}